import { AndroidNoticationScheduler } from "../Notifications/Android/AndroidNoticationScheduler";
import { EditorNoticationScheduler } from "../Notifications/Editor/EditorNoticationScheduler";
import { INoticationScheduler } from "../Notifications/INoticationScheduler";
import { AndroidApplicationConfig } from "./Android/AndroidApplicationConfig";
import { Platform } from "./ApplicationConfig";
import { ApplicationListener } from "./Editor/ApplicationListener";
import { EditorAppConfig } from "./Editor/EditorAppConfig";

export interface IApplicationHandler{
    OnPauseApplication():void
    OnResumeApplication():void
}

export interface IDeviceConfig{
    GetApplicationUserId():string
    GetPreferredLanguage(langs:string[]): string 
    GetSupport():void
    GetRate():void
    GetVersion():string
    GetApplicationName():string
}

export class AppDevice {
    private _scheduler:INoticationScheduler
    private _app:ApplicationListener
    private _config:IDeviceConfig

    constructor(platform:Platform){
        this._app = new ApplicationListener()

        switch(platform)
        {
            case Platform.Android : {
                this._scheduler = new AndroidNoticationScheduler("com/grit/fftc/notifications/Scheduler"); 
                this._config = new AndroidApplicationConfig()
                break;
            }
            case Platform.Ios: {} //break;
            default:{
                this._scheduler = new EditorNoticationScheduler();
                this._config = new EditorAppConfig()
            }
        }
    }
    GetApplicationUserId():string { return this._config.GetApplicationUserId() }
    GetPreferredLanguage(langs:string[]): string  { return this._config.GetPreferredLanguage(langs) }
    GetDevice(){ return this._config }
    
    AddHandler(handler:IApplicationHandler) { this._app.AddHandler(handler); }
    RemoveHandler(handler:IApplicationHandler) { this._app.RemoveHandler(handler); }
    
    ResolveScheduler() { return this._scheduler }
}